.advantages {
	margin-bottom: 20px;
	&__wrap {
		display: flex;
		flex-direction: column; }
	&__title {
		color: #393e6b;
		font-size: 24px;
		font-weight: 300;
		text-align: center; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		list-style: none;
		padding: 0;
		margin: 0; }
	&__item {
		border-sizing: border-box;
		display: flex;
		align-items: center;
		width: 267px;
		height: 60px; }
	&__item span {
		color: #069590;
		font-size: 46px;
		font-weight: 700;
		width: 30px;
		opacity: 0.2; }
	&__item p {
		margin-top: 40px;
		margin-left: 20px;
		color: #393e6b;
		font-size: 14px;
		font-weight: 400;
		text-align: left; } }

@media (min-width: 1200px) {
	.advantages {
		margin: 100px 0;
		&__wrap {
			align-items: center; }
		&__title {
			width: 570px;
			font-size: 34px;
			margin-bottom: 80px; }
		&__list {
			height: 230px; }
		&__item p {
			width: 160px; } } }
