.title {
	display: flex;
	flex-direction: column-reverse;
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center; }
	&__img {
		margin-top: 52px;
		margin-bottom: 40px;
		height: 440px;
		background: url(../img/img_01_x1.png) no-repeat top;
		background-size: contain; }
	&__title {
		font-size: 22px;
		font-weight: 300;
		line-height: 36px;
		text-align: center; }
	&__btn {
		color: #fff;
		background: #0facbb;
		margin-top: 12px;
		margin-bottom: 32px;
		padding: 15px 50px;
		border-radius: 50px;
		text-align: center;
		transition: 0.4s;
		font-size: 16px; }
	&__btn:hover {
		background: #25c5d4; } }

@media (min-width: 1200px) {
	.title {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		height: 800px;
		&__wrap {
			align-items: flex-start; }
		&__img {
			width: 70%;
			height: 600px;
			background: url(../img/img_01_x2.png) no-repeat;
			background-size: cover; }
		&__title {
			width: 500px;
			font-size: 34px;
			text-align: left; } } }
