.footer {
	background: #242637;
	padding-top: 20px;
	padding-bottom: 20px;
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-size: 14px;
		font-weight: 300; }
	&__wrap p {
		color: #7b7fa8;
		margin: 0;
		margin-bottom: 5px; }
	&__wrap a {
		color: #2ee3f4; }
	&__wrap a:hover {
		color: #25c5d4; } }
