.nav {
	display: flex;
	flex-direction: column;
	&__wrap-1 {
		display: flex;
		justify-content: space-between; }
	&__wrap-1 img {
		width: 70px; }
	&__btn {
		position: relative;
		width: 40px;
		border: none;
		background-color: transparent;
		cursor: pointer;
		z-index: 100; }
	&__btn span,
	&__btn span:before,
	&__btn span:after {
		position: absolute;
		top: calc(50% - 1px);
		left: calc(50% - 10px);
		width: 20px;
		height: 2px;
		background-color: #fff;
		transition: all 0.4s; }
	&__btn span:before {
		content: '';
		top: 5px; }
	&__btn span:after {
		content: '';
		top: -5px; }
	&__btn--active span {
		background-color: transparent; }
	&__btn--active span:before {
		top: 0;
		transform: rotate(45deg); }
	&__btn--active span:after {
		top: 0;
		transform: rotate(-45deg); }
	&__wrap-2 {
		font-size: 14px;
		position: relative; }
	&__wrap-2::before {
		content: '';
		position: absolute;
		top: 13px;
		width: 100%;
		height: 2px;
		background: #60e5d7;
		opacity: 0.3; }
	&__wrap-2 p {
		margin: 0;
		margin-top: 22px; }
	&__wrap-2 a {
		color: #60e5d7; }
	&__wrap-2 a:hover {
		color: #25c5d4; }
	&__list {
		display: none;
		flex-direction: column;
		justify-content: flex-end;
		text-align: center;
		list-style: none;
		padding: 0;
		margin: 0;
		position: absolute;
		top: 60px;
		left: 0;
		background: linear-gradient(to bottom left, #40457a, #2c2f47);
		width: 100%;
		text-transform: uppercase; }
	&__list--active {
		display: flex; }
	&__item {
		display: block;
		padding: 40px 0;
		color: #fff;
		transition: 0.4s; }
	&__item:hover {
		background: #2c2f47;
		color: #25c5d4; } }

@media (min-width: 1200px) {
	.nav {
		flex-direction: row;
		justify-content: space-between;
		&__wrap-1 {
			display: flex;
			justify-content: space-between; }
		&__wrap-1 img {
			width: 70px; }
		&__btn {
			display: none; }
		&__wrap-2 {
			margin-left: 40px;
			margin-right: auto; }
		&__wrap-2::before {
			content: '';
			position: absolute;
			top: 10px;
			left: -40px;
			width: 40px;
			height: 2px;
			transform: rotate(90deg); }
		&__wrap-2 p {
			margin: 0; }
		&__list {
			display: flex;
			flex-direction: row;
			position: relative;
			top: 0;
			background: transparent;
			width: auto;
			font-size: 12px; }
		&__item {
			padding: 10px 20px;
			border-radius: 50px;
			margin-left: 10px; }
		&__item:hover {
			background: #fff;
			color: #393e6b; } } }
