.registration {
	background: linear-gradient(to left, #24b9c4, #00a1a4);
	&__title {
		color: #fff;
		font-size: 24px;
		font-weight: 700;
		text-align: center;
		padding-top: 40px;
		padding-bottom: 20px;
		margin: 0;
		text-transform: uppercase; }
	&__form {
		display: flex;
		flex-direction: column; }
	&__inputs {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 263px; }
	&__inputs input {
		padding: 10px 25px;
		padding-top: 7px;
		border-radius: 50px;
		border: none; }
	&__inputs input::placeholder {
		opacity: 0.6;
		font-size: 14px;
		color: #393e6b; }
	&__checkbox {
		margin-top: 30px;
		display: flex; }
	&__checkbox input {
		margin-top: -10px;
		width: 30px;
		height: 30px;
		border: none; }
	&__checkbox label {
		margin-left: 10px;
		color: #fff;
		font-size: 15px;
		font-weight: 400; }
	&__checkbox label a {
		color: #2ee3f4; }
	&__checkbox label a:hover {
		color: #25c5d4; }
	&__btn {
		border: none;
		margin: 20px 0;
		padding: 10px;
		border-radius: 50px;
		background: #393e6b;
		color: #fff;
		font-size: 12px;
		font-weight: 700;
		text-transform: uppercase;
		cursor: pointer;
		transition: 0.4s; }
	&__btn:hover {
		background: #242637; } }

@media (min-width: 1200px) {
	.registration {
		&__title {
			font-size: 36px;
			margin-bottom: 10px; }
		&__form {
			width: 600px;
			margin: auto; }
		&__inputs {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
			height: 213px;
			margin-bottom: 30px; }
		&__inputs input {
			width: 240px;
			height: 30px; }
		&__inputs input:last-child {
			width: 100%; }
		&__checkbox {
			width: 470px;
			margin: auto; }
		&__checkbox input {
			margin-top: -18px;
			width: 55px;
			height: 55px; }
		&__btn {
			border: none;
			margin: 60px auto;
			padding: 20px;
			font-size: 15px;
			width: 300px; } } }
