.nav {
  display: flex;
  flex-direction: column; }
  .nav__wrap-1 {
    display: flex;
    justify-content: space-between; }
  .nav__wrap-1 img {
    width: 70px; }
  .nav__btn {
    position: relative;
    width: 40px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    z-index: 100; }
  .nav__btn span,
  .nav__btn span:before,
  .nav__btn span:after {
    position: absolute;
    top: calc(50% - 1px);
    left: calc(50% - 10px);
    width: 20px;
    height: 2px;
    background-color: #fff;
    transition: all 0.4s; }
  .nav__btn span:before {
    content: '';
    top: 5px; }
  .nav__btn span:after {
    content: '';
    top: -5px; }
  .nav__btn--active span {
    background-color: transparent; }
  .nav__btn--active span:before {
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .nav__btn--active span:after {
    top: 0;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  .nav__wrap-2 {
    font-size: 14px;
    position: relative; }
  .nav__wrap-2::before {
    content: '';
    position: absolute;
    top: 13px;
    width: 100%;
    height: 2px;
    background: #60e5d7;
    opacity: 0.3; }
  .nav__wrap-2 p {
    margin: 0;
    margin-top: 22px; }
  .nav__wrap-2 a {
    color: #60e5d7; }
  .nav__wrap-2 a:hover {
    color: #25c5d4; }
  .nav__list {
    display: none;
    flex-direction: column;
    justify-content: flex-end;
    text-align: center;
    list-style: none;
    padding: 0;
    margin: 0;
    position: absolute;
    top: 60px;
    left: 0;
    background: linear-gradient(to bottom left, #40457a, #2c2f47);
    width: 100%;
    text-transform: uppercase; }
  .nav__list--active {
    display: flex; }
  .nav__item {
    display: block;
    padding: 40px 0;
    color: #fff;
    transition: 0.4s; }
  .nav__item:hover {
    background: #2c2f47;
    color: #25c5d4; }

@media (min-width: 1200px) {
  .nav {
    flex-direction: row;
    justify-content: space-between; }
    .nav__wrap-1 {
      display: flex;
      justify-content: space-between; }
    .nav__wrap-1 img {
      width: 70px; }
    .nav__btn {
      display: none; }
    .nav__wrap-2 {
      margin-left: 40px;
      margin-right: auto; }
    .nav__wrap-2::before {
      content: '';
      position: absolute;
      top: 10px;
      left: -40px;
      width: 40px;
      height: 2px;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg); }
    .nav__wrap-2 p {
