.owl-prev {
  position: absolute;
  top: calc(50% - 55px);
  left: -100px;
  width: 70px;
  height: 90px; }

.owl-next {
  position: absolute;
  top: calc(50% - 55px);
  right: -100px;
  width: 70px;
  height: 90px; }

.owl-prev:before,
.owl-next:before {
  content: '';
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 10px);
  width: 30px;
  height: 50px; }

.owl-prev:before {
  background: url(../img/arrow_prev.svg) no-repeat; }

.owl-next:before {
