.partners {
	background-color: #f5f5fa;
	padding-bottom: 20px;
	&__title {
		color: #393e6b;
		font-size: 18px;
		font-weight: 700;
		opacity: 0.3;
		text-align: center;
		padding-top: 20px;
		text-transform: uppercase; }
	&__sliders {
		margin: auto;
		width: 40%; } }

.owl-prev {
	position: absolute;
	top: calc(50% - 55px);
	left: -100px;
	width: 70px;
	height: 90px; }
.owl-next {
	position: absolute;
	top: calc(50% - 55px);
	right: -100px;
	width: 70px;
	height: 90px; }
.owl-prev:before,
.owl-next:before {
	content: '';
	position: absolute;
	top: calc(50% - 20px);
	left: calc(50% - 10px);
	width: 30px;
	height: 50px; }
.owl-prev:before {
	background: url(../img/arrow_prev.svg) no-repeat; }
.owl-next:before {
	background: url(../img/arrow_next.svg) no-repeat; }

@media (min-width: 1200px) {
	.partners {
		padding-top: 40px;
		padding-bottom: 60px;
		&__title {
			font-size: 28px;
			margin-bottom: 70px; }
		&__sliders {
			width: 90%; } } }
